*{
    font-family: Arial, Helvetica, sans-serif;
  }
  
  .scoresheet {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100dvh;
    width: 100vw;
    background-image: url('images/background_v2.svg');
    background-position: center;
    background-size:cover;
    background-attachment: fixed;
  }
  
  .roundHeader{
    display: flex;
    align-items: center;
    height: 8dvh;
    font-size: 30px;
    font-weight: bold;
  }

  .playerscore{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 77dvh;
  }

.user{
  height: 3em;
  width: 80%;
  background-color: rgb(51, 47, 47);
  color:rgb(175, 171, 171);
  margin: 5px;
  border-radius: 10px;
  padding-left: 5%;
  padding-right: 5%;
  font-size: 20px;
}

.usercontent{  
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  width: 100%;
}

  nav{
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    height: 10dvh;
  }

  .homepage{
    display: flex;
    color:rgb(193, 220, 231);
    background-color: rgb(57, 78, 168);
    justify-content: center;
    align-items: center;
    font-size: 5vw;
    height: 95%;
    width: 40%;
    border-radius: 10px;
    margin: 10 px;
    text-decoration: none;
  }

  .battlepage{
    display: flex;
    color:rgb(193, 220, 231);
    background-color: rgb(20, 80, 110);
    justify-content: center;
    align-items: center;
    font-size: 5vw;
    height: 95%;
    width: 40%;
    border-radius: 10px;
    margin: 10 px;
    text-decoration: none;
  }

  .reset{
    display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(199, 55, 33);
  color:rgb(211, 211, 211);
  height: 5dvh;
  width: 20vw;
  border-radius: 20px;
  font-size:2dvh;
  margin-bottom: 10px;
  }
  