*{
    font-family: Arial, Helvetica, sans-serif;
  }

.home{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100dvh;
    background-image: url('images/background_v2.svg');
    background-position: center;
    background-size:cover;
    background-attachment: fixed;
  }
  
  .logo{
    background-image: url('images/logo_v3.svg');
    background-position: center;
    background-repeat: no-repeat;
    width: 85%;
    height: 7dvh;
    background-size:contain;
    
  }

  .playertable{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 80%;
    height: 83dvh;
  
  }  
  
  .players{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 80%;
    align-items: center;
  }
  
  .player{
    color:rgb(193, 220, 231);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 3%;
    padding-right: 2%;
    width: 100%;
    height: 3em;
    background-color: rgb(51, 47, 47);
    margin: 5px;
    border-radius: 10px;
    font-size: 20px;
  }
  
   .addplayer{
    aspect-ratio: 1/1;
    height: 7dvh;
    background-image: url('images/plus_button.svg');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
  }
  
  .removeplayer{
    aspect-ratio: 1/1;
    height: 75%;
    display: flex;
    justify-content: center;
    border-radius: 50%;
    background-image: url('images/x_button.svg');
    background-position: center;
    background-size: contain;
  }
  
  .start_rules{
    display: flex;
    width: 100%;
    height: 10dvh;
    justify-content: space-evenly;
  }

  .rulesBtn{
    display: flex;
    color:rgb(193, 220, 231);
    background-color: rgb(57, 78, 168);
    justify-content: center;
    align-items: center;
    font-size: 5vw;
    height: 95%;
    width: 40%;
    border-radius: 10px;
    margin: 10 px;
  }

  .start{
    display: flex;
    color:rgb(193, 220, 231);
    background-color: rgb(5, 15, 20);
    align-items: center;
    font-size: 5vw;
    height: 95%;
    width: 40%;
    border-radius: 10px;
    margin: 10 px;
    text-decoration: none;
  }
  
  .start > *{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 4.4vw;
    background-color: rgb(20, 80, 110);
    color:rgb(193, 220, 231);
    text-align: center;
    align-items: center;
    font-size: 5vw;
    height: 100%;
    width: 100%;
    border-radius: 10px;
    margin: 10 px;
    text-decoration: none;
  }

  