*{
    font-family: Arial, Helvetica, sans-serif;
  }
  

.fancyButton{
    background-color: #2c3369;
    border: none;
    color: rgb(182, 192, 196);
    padding: 5px 20px;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    border-radius:5px;
    
}

.update{
    display: flex;
    justify-content: space-evenly;
    width:100%;
  }

.modal_score {
    position: fixed;
    z-index: 1;
    height: 100%;
    width: 100%;
    left:0;
    top: 0;
    background-color: rgba(24, 64, 116, 0.5);
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
  }
  
.modal_content_score {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items:center;
    text-align: center;
    background-color: rgb(113, 123, 182);
    color: rgb(18, 29, 92);
    font-size: xx-large;
    width: 90%;
    height:20%;
    border-radius: 20px;
  }