*{
    font-family: Arial, Helvetica, sans-serif;
  }
  
  .battle {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    height: 100dvh;
    width: 100vw;
    background-image: url('images/background_v2.svg');
    background-position: center;
    background-size:cover;
    background-attachment: fixed;
  }

  h1{
    margin: 0;
  }
  .game{
    margin: 0;
  }
  
  .active-players{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    height: 25%;
  }
  
  .A{
    display: inline-block;
    align-items: center;
    justify-content: center;
    aspect-ratio: 1/1;
    max-width: 50%;
    height: 80%;
    color: rgb(167, 50, 50);
    background-color: rgb(167, 50, 50);
    border-radius: 10px;
    padding: 5px;
    text-align: center;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
  }
  
  
  .B{
    display: inline-block;
    align-items: center;
    justify-content: center;
    aspect-ratio: 1/1;
    max-width: 50%;
    height: 80%;
    color: rgb(167, 50, 50);
    background-color: rgb(56, 56, 165);
    border-radius: 10px;
    padding: 5px;
    text-align: center;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
  }

  .rules{
    display: flex;
    align-items: center;
    text-wrap: wrap;
    padding-right:5%;
    padding-left: 5%;
    width: 80%;
    height: 35%;
    color: rgb(18, 17, 39);
    background-color: rgb(197, 216, 224);
    border-radius: 10px;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
  }
  
  .extra{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    height: 10%;
  }
  
  .misc{
    height: 100%;
    width: 95%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }

  .go{
    display: flex;
    justify-content: center;
    align-items: center;
    aspect-ratio: 1/1;
    height: 80%;
    border-radius: 50%;
    border: 1px solid;
    background-color: rgb(96, 158, 60);
    visibility: hidden;
  }
  
  nav{
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    align-items: center;
  }


  .A > *{
    height: 100%;
    width:100%;
    border-radius: inherit;
  }

  .B > *{
    height: 100%;
    width:100%;
    border-radius: inherit;
  }


  .sides{
    height: 100%;
    width:100%;
   display: flex;
   align-items: center;
   justify-content: center;
   border-radius: inherit;
   color: black;
  text-decoration: none;
  font-size: 24px;

  }
  .sides > span{
    white-space: pre-wrap;
  overflow: hidden;
  text-overflow: ellipsis;
  }

  .redice{
    text-align: center;
    color:red;
    font-size: 30px;
    font-weight: bolder;
    height: 50%;
  }
  .blueice{
    text-align: center;
    color:blue;
    font-size: 30px;
    font-weight: bolder;
    height: 50%;
  }

  .light{
    height: 100%;
    width: 60%;
    border-radius: 10px;
  }

  .hill_players{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-around;
    
  }

  .hill_players>*{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    font-weight: bolder;
    width:45%;
    height: 45%;
    border-radius: 10px;
    background-color: rgb(156, 181, 193);
  }

  .hill_name{
    max-width: 60%;
    white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  }