*{
    font-family: Arial, Helvetica, sans-serif;
  }

.ash{
  font-size: 30px;
  font-weight: bold;
}

.modal_active{
  display: flex;
    flex-direction: column;
    align-items:center;
    background-color: rgb(113, 123, 182);
    width: 90%;
    height:80%;
    border-radius: 20px;
}

.modal_active >*{
  margin: 5px;

}
  
  .calloutcontent{  
    height: 2.5em;
  width: 80%;
  background-color: rgb(79, 86, 99);
  color:rgb(175, 171, 171);
  border-radius: 10px;
  padding-left: 5%;
  padding-right: 5%;
  font-size: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  }

  .modal_content_callout{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items:center;
    background-color: rgb(113, 123, 182);
    width: 90%;
    height:70%;
    border-radius: 20px;
  }

.modal_add,.modal_rules,.modal_callout{
    position: fixed;
    z-index: 1;
    height: 100%;
    width: 100%;
    left:0;
    top: 0;
    background-color: rgba(24, 64, 116, 0.5);
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
  }
  
.modal_content_add {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items:center;
    text-align: center;
    background-color: rgb(113, 123, 182);
    width: 90%;
    height:20%;
    border-radius: 20px;
  }

  .modal_content_rules {
    display: flex;
    flex-direction: column;
    align-items:center;
    background-color: rgb(113, 123, 182);
    width: 90%;
    height:auto;
    border-radius: 20px;
  }
 

  .modal_content_rules > :not(.tips){
    margin: 15px;
    text-align: left;
  }

  .tips{
    margin: 15px;
    text-align: center;
  }

  ul{
    padding-left: 15px;
    padding-right: 15px;
    text-align: left;
    margin: 0;
  }

input[type=text] {
    background-color: #2c3369;
    color: rgb(182, 192, 196);
    border: none;
    border-radius: 5px;
    height: 5dvh;
    font-size: 16px;
    padding: 0px 10px;
    margin-bottom: 20px;
    width: 90%;
    text-align: center;
}

input[type=text]:focus {
    outline:none;
}

.fancyButton{
    background-color: #2c3369;
    border: none;
    color: rgb(182, 192, 196);
    padding: 5px 20px;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    border-radius:5px;
    
}

.fancyButton_add{
    position: relative;
    background-color: #2c3369;
    border: none;
    color: rgb(182, 192, 196);
    padding: 5px 20px;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    border-radius:5px;
    
}


